import React from "react";

export const Locations = () => {
  const data = useLoaderData();

  return (
    <section id="locations">
      <div className="container">
        <h1>{data[0]?.name}</h1>
        {data[0].content.map((children) => {
          let i = 0;

          i++;
          return (
            <p key={i}>
              {children.children[0].text}
              <br />
            </p>
          );
        })}
      </div>
    </section>
  );
};

export default Locations;
